import React from 'react'
import 'tachyons';

const Card = ({ name, email, id }) => {
    // const { name, email, id } = props
  return (
    <div className='tc bg-light-green dib br1 pa3 ma2 grow bw2 shadow-5 br4'>
      <img alt='robot' src={`https://robohash.org/${id}?200x200`}/>
      <div>
        <h2>{name}</h2>
        <p>{email}</p>
      </div>
    </div>
  )
}

export default Card
